<template>
  <transition name="component-fade" mode="out-in">
    <ContactForm :headerText="$text('confirmYourData')">
      <!--  HEADER CONTENT  -->
      <div style="width:100%" slot="header-content">
        <v-btn @click="backBtn" class="back-btn" icon>
          <v-icon class="theme-bg-color">mdi-chevron-left</v-icon>
        </v-btn>
      </div>

      <!--  BODY CONTENT  -->
      <div slot="body-content">
        <v-row align="center" justify="center">
          <v-col cols="6">
            <v-text-field
              class="white text-field"
              v-model="setUserName"
              :color="outlineColor"
              outlined
              :label="$text('userName')"
              :rules="rules"
              hide-details="auto"
            ></v-text-field>
          </v-col>
          <v-col cols="6">
            <v-text-field
              class="white text-field"
              :rules="rules"
              v-model="setEmail"
              :color="outlineColor"
              outlined
              :label="$text('yourEmail')"
          ></v-text-field>
          </v-col>
          <v-col cols="6">
            <v-text-field
              disabled
              class="white text-field"
              :rules="rules"
              v-model="formData.category"
              :color="outlineColor"
              outlined
              :label="$text('category')"
          ></v-text-field>
          </v-col>
          <v-col cols="6">
            <v-text-field
              disabled
              class="white text-field"
              v-model="formData.subject"
              :color="outlineColor"
              outlined
              :label="$text('subject')"
              :rules="rules"
              hide-details="auto"
            ></v-text-field>
          </v-col>
          <v-col cols="12">
            <div class="white px-2 py-2 msg-container">
              <div class="subheading bold-text pb-1">
                {{ $text('yourMessage') }}
              </div>
              {{ getMessage() }}
            </div>
          </v-col>
        </v-row>

        <v-row class="px-4 pt-1 mt-2" align="center">
          <v-btn @click="sendMessage" :disabled="!sendBtnEnabled" depressed block class="theme-bg">
            {{ $text('sendMessageBtn') }}
          </v-btn>
        </v-row>

      </div>
    </ContactForm>
  </transition>
</template>

<script>
import ContactForm from '@/components/ContactForm'
import { mapActions, mapState } from 'vuex'
import { sendMessageHelper } from '@/utils/helpers'
import { statusTypes } from '@/utils/types'

export default {
  name: 'Confirm-page',
  components: {
    ContactForm
  },
  data: () => ({
    rules: [
      value => !!value || 'Required.',
      value => (value && value.length >= 3) || 'Min 3 characters'
    ]
  }),
  created () {
    process.env.NODE_ENV !== 'production' && console.log(this.formData)
  },
  computed: {
    ...mapState({
      textKeys: ({ appState }) => appState.textKeys,
      formData: ({ userData }) => userData.formData,
      extraUserData: ({ userData }) => userData.extraUserData
    }),
    setUserName: {
      get: function () {
        return this.formData.userName
      },
      set: function (val) {
        this.setFormData({ userName: val })
      }
    },
    setEmail: {
      get: function () {
        return this.formData.email
      },
      set: function (val) {
        this.setFormData({ email: val })
      }
    },
    sendBtnEnabled () {
      const { formData } = this
      return formData.userName.length > 3 && formData.email.length > 3
    },
    outlineColor () {
      return getComputedStyle(document.body).getPropertyValue('--theme-bg')
    }
  },
  methods: {
    ...mapActions([
      'setFormData',
      'setSendingMsg',
      'setTicketStatus'
    ]),
    getMessage () {
      const { message } = this.formData
      const maxLenght = 485
      if (message.length > maxLenght) {
        return message.substr(0, maxLenght) + '...'
      } else {
        return message
      }
    },
    backBtn () {
      this.$router.push({ path: '/', query: this.$route.query })
        .catch(error => {
          console.info(error.message)
        })
    },
    successCallback () {
      this.setTicketStatus(statusTypes.SUCCESS)
    },
    failCallback () {
      this.setTicketStatus(statusTypes.FAILED)
    },
    async sendMessage () {
      this.setSendingMsg(true)
      try {
        const res = await sendMessageHelper(this.formData, this.extraUserData)
        process.env.NODE_ENV !== 'production' && console.log(res)
        if (res.status === 200) {
          this.successCallback()
        } else {
          this.failCallback()
        }
      } catch (err) {
        this.failCallback()
      }
      this.setSendingMsg(false)
      this.$router.push({ path: '/status', query: this.$route.query })
    }
  }
}
</script>

<style lang="scss" scoped>
.msg-container {
  border: 1px solid #bdbdbd!important;
  border-radius: 4px;
  height: 300px;
}
</style>
